const desc = `
  # Form 
   - 
   -  Form  createForm  rsForm 
`;

const base = `
  \`\`\`jsx
  import React, { Component } from 'react';
  import ReactDOM from 'react-dom';
  import { Button, Radio, Form } from 'react-stone;

  const {
    CreateForm,
    FieldInput,
    FieldCheckbox,
    FieldSelect,
    FieldRadioGroup,
    FieldTextarea,
  } = Form;

  const selectChange = (val) => {
    console.log(val);
  }

  const saleVolumes = [
    {
      label: '<1000',
      value: 1
    },
    {
      label: '1001~10000',
      value: 2
    },
    {
      label: '10001~100000',
      value: 3
    },
    {
      label: '100001~1000000',
      value: 4
    },
    {
      label: '>1000000',
      value: 5
    }
  ];

  const getContentComponent = (data) => {
    return (
      <p>{data}</p>
    )
  }

  class FieldForm extends React.Component {
    constructor(props) {
      super(props);
      this.state = {
        options1: [
          { label: 'Apple', value: 'Apple' },
          { label: 'Pear', value: 'Pear' },
          { label: 'Orange', value: 'Orange' },
        ],
        checkedBoxValue: [],
        formDate: {},
        productTypexs: null
      }
    }
    formSubmit = (e) => {
      e.preventDefault();
      this.props.rsForm.validateForm(true, () => {
        if (this.props.rsForm.isValid()) {
          console.log('Yes');
          console.log('',this.props.rsForm.getFormValues())
          Modal.open({
            "size": 'sm',
            "title": '',
            "template": getContentComponent('' + JSON.stringify(this.props.rsForm.getFormValues())),
          });
        } else {
          console.log('No')
          console.log('', this.props.rsForm.getFormValues())
        }
      })
    }
    productTypeChangexs = (val) => {
      console.log(val);
      this.setState({
        productType: val.value
      })
    }
    render() {
      return (
        <Form onSubmit={this.formSubmit}>
          <FieldInput
            required
            name="name"
            label=""
            validations={{ required: true }}
            validationErrors={{ required: '' }}
            />
          <FieldInput
            required
            name="email"
            validations={{ isEmail: true }}
            validationErrors={{ isEmail: '' }}
            label=""/>
          <FieldInput
            name=""
            label=""/>
          <FieldSelect
            name="select"
            label=""
            placeholder=""
            optionData={saleVolumes}
            onChange={selectChange}/>
          <FieldTextarea
            required
            name=""
            label=""
            validations={{ required: true }}
            validationErrors={{ required: '' }}/>
          <FieldRadioGroup
            name=""
            label=""
            value={this.state.productTypexs}
            onChange={this.productTypeChangexs}
            >
            <Radio value={1} id="productType1xx" textname=""></Radio>
            <Radio value={2} id="productType2xx" textname=""></Radio>
          </FieldRadioGroup>
          <FieldCheckbox
            required
            name=""
            label=""
            options={this.state.options1}
            value={this.state.checkedBoxValue}
            validations={{
              isMore(values, value) {
                return value.length >= 2;
              }
            }}
            validationErrors={{ isMore: '' }}/>
            <div style={{
              marginLeft: '120px',
              width: '150px',
              display: 'flex',
              justifyContent: 'space-between'
            }}>
              <Button theme='gray'></Button>
              <Button htmlType="submit"></Button>
            </div>
        </Form>
      );
    }
  }

  const WrappedForm = CreateForm()(FieldForm);

  class App extends Component {
    constructor(props) {
      super(props);
      this.state = {
      };
    }
    render() {
      return (
        <WrappedForm/>
      );
    }
  }

  ReactDOM.render(<App />, document.getElementById('root'));
  \`\`\`
`;

const api = `
  # API

  ## Form
   html  form .

     |       |          |     
  ----    | ----    | ----    | ----    |
  onSubmit  | func(e:Event)  | noop |  
  style | object | null | 
  disableEnterSubmit | boolean | true | 
  className | string | '' | 

  ## Form.createForm

  Form.createForm(options)(FormComponent)

  options :

     |       |          |     
  ----    | ----    | ----    | ----    |
  formValidations  | object  | {} |  ,  validations 

  ## rsForm
   Form.createForm  props  rsForm ,  this.props.rsForm , rsForm  API 

     |        |     
  ----    | ----    |  ----    |
  getFormValues  | func  |   form 
  getFieldError | func(name: String) |  Field , 
  isValid  | func  |   Field 
  setFieldsValue  | func(data: Object)  |   Field 
  resetFieldsValue  | func(data: Object)  |   Field 
  validateForm | func(forceValidate: Boolean, callback: Function) | 

  ##  validation rules
   Field  validations , 

  |  |  |  |
  |------|------|------|
  | required |  |  true  |
  | isExisty |  null  undefined |  |
  | matchRegex |  | Regex |
  | isEmail |  |  |
  | isUrl |  url  |  |
  | isTrue | true |  |
  | isFalse | false |  |
  | isNumeric |  |  |
  | isInt |  |  |
  | isFloat |  |  |
  | isLength |  | (Number) |
  | equals |  |  |
  | equalsField |  |  Field name(String) |
  | maxLength |  | (Number) |
  | minLength |  | (Number) |

`;

export {
  desc,
  base,
  api,
};



